import React from "react";
import { ImCheckboxChecked, ImCheckboxUnchecked } from "react-icons/im";
import { course } from "../../../../../interfaces";

interface QuestionChaptersProps {
  selectedCourse: course | null | undefined;
  addedChapters: string[];
  setAddChapter: (chapter: string) => void;
}

const QuestionChapters = (props: QuestionChaptersProps) => {
  if (props.selectedCourse === undefined || props.selectedCourse === null) {
    return (
      <div>
        <div className="text-6xl text-center text-red-800 font-bold rounded bg-red-100 px-3 py-4">
          Invalid course selected!
        </div>
      </div>
    );
  } else if (
    props.selectedCourse !== undefined &&
    props.selectedCourse !== null &&
    props.selectedCourse.chapters?.length === 0
  ) {
    return (
      <div>
        <div className="text-3xl text-center text-yellow-800 font-bold rounded bg-yellow-100 px-3 py-4">
          No chapter available for this course!
        </div>
      </div>
    );
  } else {
    return (
      <div className="px-2 pb-2">
        <div className="text-sm text-gray-500 font-bold">Selected course</div>
        <div className="bg-blue-50 text-blue-600 font-bold rounded px-3 py-2">
          {props.selectedCourse.course_code} -{" "}
          {props.selectedCourse.course_name}
        </div>
        <div className="flex flex-col gap-2 mt-3">
          {props.selectedCourse.chapters !== undefined &&
          props.selectedCourse.chapters.length === 0 ? (
            <div>No chapters found!</div>
          ) : (
            props.selectedCourse.chapters &&
            props.selectedCourse.chapters.map((item, i) => (
              <div
                key={i + 1}
                onClick={() => props.setAddChapter(item.id)}
                className={`${
                  props.addedChapters.find((itm) => itm === item.id)
                    ? "text-blue-600"
                    : "text-gray-600"
                } border rounded px-2 py-2 flex flex-row items-center gap-3 hover:border-blue-500 hover:bg-blue-50 hover:text-blue-600 cursor-pointer`}
              >
                {props.addedChapters.find((itm) => itm === item.id) ? (
                  <ImCheckboxChecked className="text-2xl cursor-pointer" />
                ) : (
                  <ImCheckboxUnchecked className="text-2xl cursor-pointer" />
                )}
                <span>{item.name}</span>
              </div>
            ))
          )}
        </div>
      </div>
    );
  }
};

export default QuestionChapters;
