import SchoolLevelsItem from './SchoolLevelsItem'
import * as Interfaces from '../../../../interfaces/index';
import { searchItem } from '../ApplicationPositions';
import Loading from '../../../../components/Loading/Loading';

interface SchoolLevelsFilterPropsInterface {
  educationCategories: Interfaces.education_category[] | null;
  schoolLevels: Interfaces.school_level[] | null;
  searchLevel: searchItem | null;
  setSearchLevel: (educationCategory: Interfaces.education_category | null, schoolLevel: Interfaces.school_level | null) => void
  setSchoolLevelFilterFn: (status: boolean) => void;
}

const SchoolLevelsFilter = (props: SchoolLevelsFilterPropsInterface) => {
  return (
    <div className="col-span-12 md:col-span-4 md:static w-full top-2">
      <div className="bg-white shadow rounded-sm px-2 py-2">
        {console.log(props.educationCategories)}
        {props.educationCategories === null ? <Loading /> :
        props.educationCategories.length === 0 ? "No Education categories available" :
        props.educationCategories.map((item, i) => (
          <SchoolLevelsItem 
            key={i + 1} educationCategoryDetails={item} 
            schoolLevels={props.schoolLevels}
            searchLevel={props.searchLevel}
            setSearchLevel={props.setSearchLevel}
            />
        ))}
      </div>
    </div>
  )
}

export default SchoolLevelsFilter
