import React, { useState } from "react";
import { MdLanguage } from "react-icons/md";
import { RiCloseCircleFill } from "react-icons/ri";
import {
  LanguageInterface,
  QuestionLanguage,
  QuestionLanguages,
} from "../../interfaces/question";
import { CgMoreO } from "react-icons/cg";

interface ChangeLanguageProps {
  setSelectedLanguage: (lang: LanguageInterface) => void;
  selectedLanguage: LanguageInterface;
  languagesList?: QuestionLanguage[];
}

const ChangeLanguage = (props: ChangeLanguageProps) => {
  const [visible, setVisible] = useState<boolean>(false);
  // const [minimize, setMinimize] = useState<boolean>(true);
  return (
    <div
      className={`fixed bottom-0 z-50 left-1 bg-blue-600 ${
        visible === false ? "text-blue-800" : "text-white"
      } hover:text-white rounded-t-lg w-auto ${
        visible === true
          ? "animate__animated animate__fadeInUp animate__faster"
          : "animate__animated animate__fadeInDown animate__faster"
      } bg-white`}
    >
      <div
        className={`flex flex-col px-3 py-2 rounded-t-lg ${
          visible === true
            ? "bg-blue-700"
            : "bg-white shadow-lg border border-gray-300"
        } hover:bg-blue-800 cursor-pointer`}
      >
        <div className="flex flex-row items-center justify-between">
          <div
            onClick={() => setVisible(!visible)}
            className="font-bold text-lg flex flex-row gap-2 items-center"
          >
            <MdLanguage className="text-2xl" />
            Choose language
          </div>
          {visible === false ? (
            <div className="flex flex-row items-center">
              <CgMoreO
                onClick={() => setVisible(!visible)}
                className="text-2xl ml-3"
              />
              {/* <AiOutlineMinusCircle onClick={() => setMinimize(false)} className="text-2xl" /> */}
            </div>
          ) : (
            <RiCloseCircleFill
              onClick={() => setVisible(!visible)}
              className="text-3xl ml-3 -my-5 text-yellow-300"
            />
          )}
        </div>
      </div>
      {visible && (
        <div className={`flex flex-col`}>
          <div className="">
            {props.languagesList === undefined
              ? QuestionLanguages
              : QuestionLanguages.filter(
                  (itm) =>
                    props.languagesList &&
                    props.languagesList.find((test) => test === itm.key)
                ).map((item, i) => (
                  <div
                    onClick={() => props.setSelectedLanguage(item)}
                    key={i + 1}
                    className={`flex flex-row items-center justify-start ${
                      props.selectedLanguage.key === item.key
                        ? "text-blue-600 bg-blue-50 rounded m-2 font-bold animate__animated animate__zoomIn animate__faster"
                        : "rounded m-2 text-white"
                    } text-center text-sm px-3 py-2 cursor-pointer hover:bg-blue-50 hover:text-blue-600 hover:border-blue-600`}
                  >
                    {item.value}
                  </div>
                ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default ChangeLanguage;
