import React from "react";
import { MdBookmark } from "react-icons/md";
import { RiShieldCheckFill } from "react-icons/ri";
import {
  LanguageInterface,
  QuestionAnswerInterface,
  QuestionItemInterface,
} from "../../../../../../interfaces/question";
import { validateCalculatedFormula } from "../../../../../../utils/question_bank";
import { SelectedKeysEdit } from "../EditQuestion";
import RunTest from "../RunTest/RunTest";

interface SimpleCalculateProps {
  selectedKey: SelectedKeysEdit | null;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerSingleMultiple: (
    question: QuestionItemInterface,
    editedAnswerIndex: string,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  updateAnswerSimpleCalculate: (
    question: QuestionItemInterface,
    editedAnswerIndex: string,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  editSingleMultipleStatus: (answer_id: string, status: boolean) => void;
  removeAnswerSingleMultiple: (
    question: QuestionItemInterface[],
    removedAnswer: QuestionAnswerInterface
  ) => void;
  selectedQuestionItem: QuestionItemInterface;
}

const SimpleCalculate = (props: SimpleCalculateProps) => {
  return (
    <div className="">
      <div
        className={`${
          props.selectedKey === SelectedKeysEdit.QuestionAnswer
            ? ""
            : "bg-blue-50"
        } -mt-3 px-2 pt-2 pb-1 rounded`}
      >
        {props.selectedQuestionItem.answer.map((item, i) => (
          <div
            key={i + 1}
            className={`flex flex-row items-center justify-between mb-2 gap-3 ${
              props.selectedKey !== SelectedKeysEdit.QuestionAnswer
                ? "text-blue-600 font-bold"
                : ""
            }`}
          >
            {props.selectedKey === SelectedKeysEdit.QuestionAnswer ? (
              <div className="flex flex-col w-full">
                <input
                  onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                    let check = props.wholeQuestion.find(
                      (itm) => itm.language === props.selectedLanguage.key
                    );
                    if (check === undefined) {
                      check = props.wholeQuestion[0];
                    }
                    props.updateAnswerSimpleCalculate(check, item.value, {
                      value: e.target.value,
                      status: item.status,
                      id: item.id,
                    });
                  }}
                  value={`${item.value}`}
                  type="text"
                  className={`w-full border-2 ${
                    validateCalculatedFormula(item.value).status === true
                      ? "input-gray-lg border-gray-500 focus:border-blue-600"
                      : "input-gray-lg-danger border-yellow-600 focus:border-yellow-700"
                  }`}
                />
                <span
                  className={`${
                    validateCalculatedFormula(item.value).status === true
                      ? "text-green-600"
                      : "text-yellow-700"
                  } text-xs font-bold`}
                >
                  {validateCalculatedFormula(item.value).msg}
                </span>
              </div>
            ) : (
              <div className="w-full flex flex-row items-center gap-3 py-2">
                {item.status === true ? (
                  <RiShieldCheckFill className="text-2xl text-blue-600" />
                ) : (
                  <MdBookmark className="text-2xl text-gray-500" />
                )}
                {item.value}
              </div>
            )}
          </div>
        ))}
      </div>
      <RunTest />
    </div>
  );
};

export default SimpleCalculate;
