import { useState } from "react";
import * as Interfaces from "../../../../interfaces/index";
import { FiChevronDown, FiChevronRight } from "react-icons/fi";
import { searchItem } from "../ApplicationPositions";

interface SchoolLevelsItemPropsInterface {
  educationCategoryDetails: Interfaces.education_category;
  schoolLevels: Interfaces.school_level[] | null;
  searchLevel: searchItem | null;
  setSearchLevel: (
    educationCategory: Interfaces.education_category | null,
    schoolLevel: Interfaces.school_level | null
  ) => void;
}

const SchoolLevelsItem = (
  props: SchoolLevelsItemPropsInterface
): JSX.Element => {
  const [levelsDropDown, setLevelsDropDown] = useState<boolean>(false);
  const initializeItem = (levelsDropDown: boolean, props: any) => {
    if (
      (props.searchLevel !== null && props.searchLevel.schoolLevel === null) ||
      (props.searchLevel !== null &&
        props.educationCategoryDetails.education_institute_id !==
          props.searchLevel.educationCategory.education_institute_id) ||
      props.setSearchLevel === null
    ) {
      levelsDropDown === false && props.setSearchLevel(null, null);
      levelsDropDown === false &&
        props.setSearchLevel(props.educationCategoryDetails, null);
    }
  };
  const activateItem = (levelsDropDown: boolean, props: any) => {
    props.setSearchLevel(null, null);
    props.setSearchLevel(props.educationCategoryDetails, null);
  };
  const activateLevelItem = (
    educationCate: Interfaces.education_category,
    item: Interfaces.school_level
  ) => {
    props.setSearchLevel(null, null);
    props.setSearchLevel(educationCate, item);
  };
  return (
    <div className="flex flex-col">
      <div
        onClick={() => {
          setLevelsDropDown(!levelsDropDown);
          initializeItem(levelsDropDown, props);
        }}
        className="flex flex-row justify-between items-center py-2 px-2 bg-blue-100 rounded cursor-pointer hover:bg-blue-200 mb-2"
      >
        <div className="flex flex-col pl-1">
          <span className="uppercase text-blue-600 text-lg font-bold">
            {props.educationCategoryDetails.education_category_title}
          </span>
          <span className="text-xs">
            {props.educationCategoryDetails.description}
          </span>
        </div>
        <div className="px-1 py-1 rounded-full flex items-center justify-center cursor-pointer text-blue-600 bg-blue-100 hover:bg-white hover:text-blue-800">
          {levelsDropDown === false ? (
            <FiChevronRight className="text-2xl" />
          ) : (
            <FiChevronDown className="text-2xl" />
          )}
        </div>
      </div>
      {levelsDropDown === true && (
        <div className="flex flex-col">
          <div
            onClick={() => activateItem(levelsDropDown, props)}
            className={`flex flex-row justify-between items-center py-2 px-2 bg-blue-50 rounded cursor-pointer hover:bg-blue-50 mb-2 ml-5 border ${
              props.searchLevel !== null &&
              props.searchLevel.educationCategory !== null &&
              props.searchLevel.educationCategory.education_institute_id ===
                props.educationCategoryDetails.education_institute_id &&
              props.searchLevel.schoolLevel === null
                ? "border-blue-500"
                : "border-blue-100"
            } hover:border-blue-500`}
          >
            <div className="flex flex-col pl-1">
              <span className="text-blue-700 text-sm font-bold">
                All levels
              </span>
            </div>
          </div>
          {/* {console.log("Institute: ", props.educationCategoryDetails)} */}
          {props.schoolLevels === null
            ? "Loading..."
            : props.schoolLevels.length === 0
            ? "No levels found"
            : props.schoolLevels.map(
                (item, i) =>
                  item.education_institute.find(
                    (itm) =>
                      itm ===
                      props.educationCategoryDetails.education_institute_id
                  ) && (
                    <div
                      onClick={() =>
                        activateLevelItem(props.educationCategoryDetails, item)
                      }
                      key={i + 1}
                      className={`flex flex-row justify-between items-center py-2 px-2 bg-blue-50 rounded cursor-pointer hover:bg-blue-50 mb-2 ml-5 border ${
                        props.searchLevel !== null &&
                        props.searchLevel.schoolLevel !== null &&
                        props.searchLevel.schoolLevel === item &&
                        props.searchLevel.educationCategory !== null &&
                        props.searchLevel.educationCategory
                          .education_institute_id ===
                          props.educationCategoryDetails.education_institute_id
                          ? "border-blue-500"
                          : "border-blue-100"
                      } hover:border-blue-500`}
                    >
                      <div className="flex flex-col pl-1">
                        <span className="text-blue-700 text-sm font-bold">
                          {item.school_level_name}
                        </span>
                      </div>
                    </div>
                  )
              )}
        </div>
      )}
    </div>
  );
};

export default SchoolLevelsItem;
