import React from "react";
import EditorComponent from "../../../../../components/EditorComponent/EditorComponent";
import {
  LanguageInterface,
  QuestionItemInterface,
} from "../../../../../interfaces";
import { SelectedKeysEdit } from "./EditQuestion";

interface HandleInputProps {
  languages: LanguageInterface[];
  questionItems: QuestionItemInterface[];
  selectedKey: SelectedKeysEdit;
  handleUpdateQuestionDetails: (question: QuestionItemInterface) => void;
}

const HandleInput = (props: HandleInputProps) => {
  if (props.selectedKey === SelectedKeysEdit.Title) {
    return (
      <div>
        {props.languages.map((item, i) => (
          <div
            key={i + 1}
            className="flex flex-row items-center animate__animated animate__fadeInDown animate__fast"
          >
            <span className="font-bold w-24 truncate text-blue-600 flex flex-row items-center gap-2">
              {item.value}
            </span>
            <input
              key={i + 1}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                let check = props.questionItems.find(
                  (itm) => itm.language === item.key
                );
                if (check === undefined) {
                  check = props.questionItems[0];
                }
                props.handleUpdateQuestionDetails({
                  _id: check._id,
                  description: check.description,
                  language: check.language,
                  status: check.status,
                  title: e.target.value,
                  answer: check.answer,
                });
              }}
              value={
                props.questionItems.find((itm) => itm.language === item.key)
                  ?.title
              }
              type="text"
              className="input-gray-lg w-full mt-3"
            />
          </div>
        ))}
      </div>
    );
  } else if (props.selectedKey === SelectedKeysEdit.Description) {
    return (
      <div>
        {props.languages.map((item, i) => (
          <div className="flex flex-col items-start animate__animated animate__fadeInDown animate__fast bg-gray-100 mb-4 p-2 rounded">
            <span className="font-bold w-24 truncate text-blue-600 flex flex-row items-center gap-2">
              {item.value}
            </span>
            <EditorComponent
              key={i + 1}
              id={props.selectedKey + i + 1}
              item={
                props.questionItems.find((itm) => itm.language === item.key) ===
                undefined
                  ? ""
                  : props.questionItems.find(
                      (itm) => itm.language === item.key
                    )!.description
              }
              handleItem={(e: any) => {
                let check = props.questionItems.find(
                  (itm) => itm.language === item.key
                );
                if (check === undefined) {
                  check = props.questionItems[0];
                }
                props.handleUpdateQuestionDetails({
                  _id: check._id,
                  description: e,
                  language: check.language,
                  status: check.status,
                  title: check.title,
                  answer: check.answer,
                });
              }}
            />
            {/* <textarea
              key={i + 1}
              onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
                let check = props.questionItems.find(
                  (itm) => itm.language === item.key
                );
                if (check === undefined) {
                  check = props.questionItems[0];
                }
                props.handleUpdateQuestionDetails({
                  description: e.target.value,
                  language: check.language,
                  status: check.status,
                  title: check.title,
                  answer: check.answer,
                });
              }}
              value={
                props.questionItems.find((itm) => itm.language === item.key)
                  ?.description
              }
              className="input-gray-lg w-full mt-3"
            ></textarea> */}
          </div>
        ))}
      </div>
    );
  } else {
    return <div></div>;
  }
};

export default HandleInput;
