import React from 'react'
import { RiCheckboxBlankCircleLine } from 'react-icons/ri';
import { LanguageInterface, QuestionAnswerInterface, QuestionItemInterface } from '../../../../../../interfaces/question';
import { SelectedKeysEdit } from '../EditQuestion';
import { MdRadioButtonChecked } from 'react-icons/md';

interface TrueFalseProps {
  selectedKey: SelectedKeysEdit | null;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerSingleMultiple: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  editTrueFalseStatus: (answer_id: string, status: boolean) => void;
  removeAnswerSingleMultiple: (question: QuestionItemInterface[], removedAnswer: QuestionAnswerInterface) => void;
  selectedQuestionItem: QuestionItemInterface;
}

const TrueFalse = (props: TrueFalseProps) => {
  return (
    <div className="flex flex-row justify-start items-center">
      {props.selectedQuestionItem.answer.map((item, i) => (
      <div key={i + 1} className={`flex flex-row items-center justify-between mb-2 gap-3 ${props.selectedKey !== SelectedKeysEdit.QuestionAnswer ? "" : ""}`}>
        <div 
          onClick={() => {
            let check = props.wholeQuestion.find(itm => itm.language === props.selectedLanguage.key);
            if (check === undefined) {
              check = props.wholeQuestion[0];
            }
            props.editTrueFalseStatus(
              item.id, !item.status
            )
          }}
          className={`flex flex-row items-center gap-2 cursor-pointer ${item.status === true ? "text-blue-600" : "text-gray-600"} hover:text-blue-600 w-40`}>
            {item.status === false ?
            <RiCheckboxBlankCircleLine className="text-3xl cursor-pointer" /> :
            <MdRadioButtonChecked className="text-3xl cursor-pointer" />}
            <span className="font-bold ">{item.value}</span>
          </div>
      </div>))}
    </div>
  )
}

export default TrueFalse
