import React from "react";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css"; // ES6
import EditorToolbar, { formats } from "../EditorToolbar/EditorToolbar";

interface EditorComponentProps {
  id: string;
  item: string;
  handleItem: (e: string) => void;
}

// Undo and redo functions for Custom Toolbar
function undoChange(this: any) {
  this.quill.history.undo();
}
function redoChange(this: any) {
  this.quill.history.redo();
}

const EditorComponent = (props: EditorComponentProps) => {
  const modules = {
    toolbar: {
      container: `#${props.id}`,
      handlers: {
        undo: undoChange,
        redo: redoChange,
      },
    },
    history: {
      delay: 500,
      maxStack: 100,
      userOnly: true,
    },
  };
  // const getTextInQuill = (
  //   content: any,
  //   delta: any,
  //   source: any,
  //   editor: any
  // ) => {
  //   const text = editor.getText(content);
  //   props.handleItem(text);
  // };
  return (
    <div>
      <EditorToolbar id={props.id} />
      <ReactQuill
        theme="snow"
        value={props.item ?? ""}
        placeholder={"Write text here..."}
        modules={modules}
        formats={formats}
        className="rounded mb-3 validate w-full"
        onChange={(content: any, delta: any, source: any, editor: any) => {
          // const text = editor.getText(content);
          props.handleItem(content);
        }}
      />
    </div>
  );
};

export default EditorComponent;
