import React from 'react'
import { HiCheck } from 'react-icons/hi';
import { MdModeEdit } from 'react-icons/md';
import { LanguageInterface, NumericalQuestionAnswerInterface, QuestionAnswerInterface, QuestionItemInterface, questionTypeElementsType } from '../../../../../../interfaces/question';
import { SelectedKeysEdit } from '../EditQuestion';
import SimpleCalculate from '../AnswersQuestionTypes/SimpleCalculate';
import Numerical from '../AnswersQuestionTypes/Numerical';
import ShortAnswer from '../AnswersQuestionTypes/ShortAnswer';

interface EditSimpleCalculateProps {
  selectedQuestionType: questionTypeElementsType | null;
  selectedQuestionItem: QuestionItemInterface;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerSingleMultiple: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  addAnswerSingleMultiple: (question: QuestionItemInterface, addedAnswer: QuestionAnswerInterface) => void;
  removeAnswerSingleMultiple: (question: QuestionItemInterface[], removedAnswer: QuestionAnswerInterface) => void;
  selectedKey: SelectedKeysEdit | null;
  setSelectedKey: (selectedKey: SelectedKeysEdit | null) => void;
  editSingleMultipleStatus: (answer_id: string, status: boolean) => void;
  marks: number;
  updateMarks: (marks: number) => void;
  updateAnswerSimpleCalculate: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  updateAnswerNumerical: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  updateShortAnswer: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  updateAnswerNumericalValue: (question: QuestionItemInterface, answer_id: string, editedAnswer: NumericalQuestionAnswerInterface) => void;
}

const EditSimpleCalculate = (props: EditSimpleCalculateProps) => {
  return (
    <div className="flex flex-col mb-3 text-sm gap-2 bg-white px-2 py-2 rounded">
      <div className="flex flex-row items-center gap-3 animate__animated animate__fadeIn animate__fast">
        <div className="w-full flex flex-row items-center justify-between pr-2 py-1">
          <div className="text-gray-600 font-bold flex flex-row items-center ml-2 -mt-2">
            {props.selectedKey !== null && props.selectedKey === SelectedKeysEdit.QuestionAnswer ?
            "Edit Question answer" : "Question answer"}
          </div>
          <div className="flex flex-row gap-2 items-center">
            {props.selectedKey !== null && props.selectedKey === SelectedKeysEdit.QuestionAnswer &&
            <>
            <div className="flex flex-row items-center justify-end gap-2">
              <span className="font-bold text-gray-600">Marks</span>
              <input onChange={(e: React.ChangeEvent<HTMLInputElement>) => props.updateMarks(parseInt(e.target.value))} type="number" placeholder="Marks" value={props.marks} className="input-gray-lg h-10" />
            </div>
            <div onClick={() => props.setSelectedKey(null)} className="bg-blue-100 text-blue-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-200 hover:text-blue-700">
              <HiCheck className="text-3xl" />
            </div>
            </>}
            {props.selectedKey !== SelectedKeysEdit.QuestionAnswer &&
            <>
            <div className="px-4 py-2 rounded-full bg-blue-100 text-blue-600 font-bold">Marks: {props.marks}</div>
            <div onClick={() => props.setSelectedKey(SelectedKeysEdit.QuestionAnswer)} className="bg-gray-100 text-gray-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-100 hover:text-blue-600">
              <MdModeEdit className="text-3xl" />
            </div>
            </>}
          </div>
        </div>
      </div>
      <div className="rounded bg-white px-2 py-2">{console.log(props.selectedQuestionType)}
        {props.selectedQuestionType === questionTypeElementsType.SimpleCalculate && 
        <SimpleCalculate 
          selectedKey={props.selectedKey} 
          wholeQuestion={props.wholeQuestion} 
          selectedLanguage={props.selectedLanguage} 
          updateAnswerSingleMultiple={props.updateAnswerSingleMultiple} 
          editSingleMultipleStatus={props.editSingleMultipleStatus} 
          removeAnswerSingleMultiple={props.removeAnswerSingleMultiple} 
          selectedQuestionItem={props.selectedQuestionItem} 
          updateAnswerSimpleCalculate={props.updateAnswerSimpleCalculate}
        />
        }
        {props.selectedQuestionType === questionTypeElementsType.Numeric && 
        <Numerical 
          selectedKey={props.selectedKey} 
          wholeQuestion={props.wholeQuestion} 
          selectedLanguage={props.selectedLanguage} 
          updateAnswerSingleMultiple={props.updateAnswerSingleMultiple} 
          editSingleMultipleStatus={props.editSingleMultipleStatus} 
          removeAnswerSingleMultiple={props.removeAnswerSingleMultiple} 
          selectedQuestionItem={props.selectedQuestionItem} 
          updateAnswerNumerical={props.updateAnswerNumerical}
          updateAnswerNumericalValue={props.updateAnswerNumericalValue}
          updateAnswerSimpleCalculate={props.updateAnswerSimpleCalculate}
        />
        }
        {props.selectedQuestionType === questionTypeElementsType.ShortAnswer && 
        <ShortAnswer 
          selectedKey={props.selectedKey} 
          wholeQuestion={props.wholeQuestion} 
          selectedLanguage={props.selectedLanguage} 
          updateAnswerSingleMultiple={props.updateAnswerSingleMultiple} 
          editSingleMultipleStatus={props.editSingleMultipleStatus} 
          removeAnswerSingleMultiple={props.removeAnswerSingleMultiple} 
          selectedQuestionItem={props.selectedQuestionItem} 
          updateAnswerSimpleCalculate={props.updateAnswerSimpleCalculate}
          updateShortAnswer={props.updateShortAnswer}
        />
        }
      </div>
    </div>
  )
}

export default EditSimpleCalculate
