import React from "react";
import EditorComponent from "../../../../../../components/EditorComponent/EditorComponent";
import {
  LanguageInterface,
  MatchingQuestionAnswerInterface,
  QuestionAnswerInterface,
  QuestionItemInterface,
} from "../../../../../../interfaces/question";
import { toMatching } from "../../../../../DoExam/QuestionDetails/ConvertingAnswers";
import { SelectedKeysEdit } from "../EditQuestion";

interface MatchingProps {
  selectedKey: SelectedKeysEdit | null;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerSingleMultiple: (
    question: QuestionItemInterface,
    editedAnswerIndex: string,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  removeAnswerSingleMultiple: (
    question: QuestionItemInterface[],
    removedAnswer: QuestionAnswerInterface
  ) => void;
  selectedQuestionItem: QuestionItemInterface;
  editMatchingValue: (
    question: QuestionItemInterface,
    editedAnswerIndex: string,
    editedAnswer: MatchingQuestionAnswerInterface
  ) => void;
  editMatchingStatus: (
    question: QuestionItemInterface,
    answer_id: string,
    status: string
  ) => void;
}

const Matching = (props: MatchingProps) => {
  let MatchingAnswers: MatchingQuestionAnswerInterface[] = toMatching(
    props.selectedQuestionItem.answer
  );
  return (
    <div>
      {MatchingAnswers.map((item, i) => (
        <div
          key={i + 1}
          className={`flex flex-row items-center justify-between mb-2 p-3 rounded gap-3 ${
            props.selectedKey !== SelectedKeysEdit.QuestionAnswer
              ? "border border-gray-300"
              : "bg-gray-100"
          }`}
        >
          <div className="flex flex-col items-center w-full">
            {props.selectedKey === SelectedKeysEdit.QuestionAnswer ? (
              <EditorComponent
                id={"editmACHXz" + (i + 1)}
                item={`${item.value}`}
                handleItem={(data: string) => {
                  let check = props.wholeQuestion.find(
                    (itm) => itm.language === props.selectedLanguage.key
                  );
                  if (check === undefined) {
                    check = props.wholeQuestion[0];
                  }
                  props.editMatchingValue(check, item.id, {
                    value: data,
                    status: item.status,
                    id: item.id,
                  });
                }}
              />
            ) : (
              <div
                dangerouslySetInnerHTML={{ __html: item.value }}
                className="w-full gap-3 py-2"
              ></div>
            )}
            {props.selectedKey === SelectedKeysEdit.QuestionAnswer ? (
              <EditorComponent
                id={"editmACHXrr" + (i + 1)}
                item={`${item.status}`}
                handleItem={(data: string) => {
                  let check = props.wholeQuestion.find(
                    (itm) => itm.language === props.selectedLanguage.key
                  );
                  if (check === undefined) {
                    check = props.wholeQuestion[0];
                  }
                  props.editMatchingStatus(
                    props.selectedQuestionItem,
                    item.id,
                    data
                  );
                }}
              />
            ) : (
              <div className="w-full">
                <div
                  dangerouslySetInnerHTML={{ __html: item.status }}
                  className={`font-bold rounded w-full text-blue-600`}
                ></div>
              </div>
            )}
          </div>
          <div
            onClick={() => {
              props.removeAnswerSingleMultiple(props.wholeQuestion, item);
            }}
            className="btn bg-red-50 text-red-600 hover:bg-red-200 hover:text-red-800 border border-white cursor-pointer"
          >
            Remove
          </div>
        </div>
      ))}
    </div>
  );
};

export default Matching;
