import React from "react";
import {
  LanguageInterface,
  MatchingQuestionAnswerInterface,
  NumericalQuestionAnswerInterface,
  QuestionAnswerInterface,
  QuestionItemInterface,
  questionTypeElementsType,
} from "../../../../../interfaces/question";
import { SelectedKeysEdit } from "./EditQuestion";
import EditMatching from "./EditQuestionTypes/EditMatching";
import EditMultipleChoice from "./EditQuestionTypes/EditMultipleChoice";
import EditSimpleCalculate from "./EditQuestionTypes/EditSimpleCalculate";
import EditTrueFalse from "./EditQuestionTypes/EditTrueFalse";

interface QuestionAnswerProps {
  selectedQuestionType: questionTypeElementsType | null;
  selectedQuestionItem: QuestionItemInterface;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerSingleMultiple: (
    question: QuestionItemInterface,
    editedAnswerIndex: string,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  updateAnswerSimpleCalculate: (
    question: QuestionItemInterface,
    editedAnswerIndex: string,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  updateAnswerNumerical: (
    question: QuestionItemInterface,
    editedAnswerIndex: string,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  updateAnswerNumericalValue: (
    question: QuestionItemInterface,
    answer_id: string,
    editedAnswer: NumericalQuestionAnswerInterface
  ) => void;
  updateShortAnswer: (
    question: QuestionItemInterface,
    editedAnswerIndex: string,
    editedAnswer: QuestionAnswerInterface
  ) => void;
  addAnswerSingleMultiple: (
    question: QuestionItemInterface,
    addedAnswer: QuestionAnswerInterface
  ) => void;
  removeAnswerSingleMultiple: (
    question: QuestionItemInterface[],
    removedAnswer: QuestionAnswerInterface
  ) => void;
  selectedKey: SelectedKeysEdit | null;
  setSelectedKey: (selectedKey: SelectedKeysEdit | null) => void;
  editSingleMultipleStatus: (answer_id: string, status: boolean) => void;
  editTrueFalseStatus: (answer_id: string, status: boolean) => void;
  editMatchingValue: (
    question: QuestionItemInterface,
    editedAnswerIndex: string,
    editedAnswer: MatchingQuestionAnswerInterface
  ) => void;
  editMatchingStatus: (
    question: QuestionItemInterface,
    answer_id: string,
    status: string
  ) => void;
  marks: number;
  updateMarks: (marks: number) => void;
  addAnswerMatchingQuestion: (
    question: QuestionItemInterface,
    addedAnswer: MatchingQuestionAnswerInterface
  ) => void;
}

const QuestionAnswer = (props: QuestionAnswerProps) => {
  return (
    <div>
      {(props.selectedQuestionType ===
        questionTypeElementsType.SingleMultiple ||
        props.selectedQuestionType ===
          questionTypeElementsType.MultipleChoice ||
        props.selectedQuestionType ===
          questionTypeElementsType.CalculatedMultipleChoice) && (
        <EditMultipleChoice
          selectedQuestionType={props.selectedQuestionType}
          selectedQuestionItem={props.selectedQuestionItem}
          wholeQuestion={props.wholeQuestion}
          selectedLanguage={props.selectedLanguage}
          updateAnswerSingleMultiple={props.updateAnswerSingleMultiple}
          addAnswerSingleMultiple={props.addAnswerSingleMultiple}
          removeAnswerSingleMultiple={props.removeAnswerSingleMultiple}
          selectedKey={props.selectedKey}
          setSelectedKey={props.setSelectedKey}
          editSingleMultipleStatus={props.editSingleMultipleStatus}
          marks={props.marks}
          updateMarks={props.updateMarks}
        />
      )}
      {props.selectedQuestionType === questionTypeElementsType.TrueFalse && (
        <EditTrueFalse
          selectedQuestionType={props.selectedQuestionType}
          selectedQuestionItem={props.selectedQuestionItem}
          wholeQuestion={props.wholeQuestion}
          selectedLanguage={props.selectedLanguage}
          updateAnswerSingleMultiple={props.updateAnswerSingleMultiple}
          addAnswerSingleMultiple={props.addAnswerSingleMultiple}
          removeAnswerSingleMultiple={props.removeAnswerSingleMultiple}
          selectedKey={props.selectedKey}
          setSelectedKey={props.setSelectedKey}
          editTrueFalseStatus={props.editTrueFalseStatus}
          marks={props.marks}
          updateMarks={props.updateMarks}
        />
      )}
      {props.selectedQuestionType === questionTypeElementsType.Matching && (
        <EditMatching
          selectedQuestionType={props.selectedQuestionType}
          selectedQuestionItem={props.selectedQuestionItem}
          wholeQuestion={props.wholeQuestion}
          selectedLanguage={props.selectedLanguage}
          updateAnswerSingleMultiple={props.updateAnswerSingleMultiple}
          addAnswerSingleMultiple={props.addAnswerSingleMultiple}
          addAnswerMatchingQuestion={props.addAnswerMatchingQuestion}
          removeAnswerSingleMultiple={props.removeAnswerSingleMultiple}
          selectedKey={props.selectedKey}
          setSelectedKey={props.setSelectedKey}
          editMatchingValue={props.editMatchingValue}
          editMatchingStatus={props.editMatchingStatus}
          marks={props.marks}
          updateMarks={props.updateMarks}
        />
      )}
      {(props.selectedQuestionType ===
        questionTypeElementsType.SimpleCalculate ||
        props.selectedQuestionType === questionTypeElementsType.Numeric ||
        props.selectedQuestionType ===
          questionTypeElementsType.ShortAnswer) && (
        <EditSimpleCalculate
          selectedQuestionType={props.selectedQuestionType}
          selectedQuestionItem={props.selectedQuestionItem}
          wholeQuestion={props.wholeQuestion}
          selectedLanguage={props.selectedLanguage}
          updateAnswerSingleMultiple={props.updateAnswerSingleMultiple}
          updateAnswerSimpleCalculate={props.updateAnswerSimpleCalculate}
          updateAnswerNumerical={props.updateAnswerNumerical}
          updateAnswerNumericalValue={props.updateAnswerNumericalValue}
          updateShortAnswer={props.updateShortAnswer}
          addAnswerSingleMultiple={props.addAnswerSingleMultiple}
          removeAnswerSingleMultiple={props.removeAnswerSingleMultiple}
          selectedKey={props.selectedKey}
          setSelectedKey={props.setSelectedKey}
          editSingleMultipleStatus={props.editSingleMultipleStatus}
          marks={props.marks}
          updateMarks={props.updateMarks}
        />
      )}
    </div>
  );
};

export default QuestionAnswer;
